@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #2b5876; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #4e4376,
    #2b5876
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.App {
  text-align: center;
  height: 100vh;
}

.App-header {
  background-color: #282c3417;
  max-height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
  color: whitesmoke;
  text-shadow: 2px 2px 5px rgb(17, 17, 17);
}

.title {
  font-family: "Dancing Script", cursive;
  font-size: calc(25px + 3vmin);
  color: white;
  text-shadow: 2px 2px 5px rgb(17, 6, 6);
}

.content {
  min-width: 70vw;
  margin: auto;
  width: 50%;
}

@media screen and (max-width: 560px) {
  .content {
    min-width: 90vw;
  }
}

.MuiPaper-root {
  padding: 10px 5px;
  color: black;
}
.goalList {
  /* overflow: scroll; */
  max-height: 80vh;
}
.goal {
  /* display: flex;
  justify-content: space-evenly; */
  margin: 10px 10px;
  max-height: 60px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.075), 0 6px 6px rgba(0, 0, 0, 0.137);
}

.goal-contents {
  padding-left: 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.goal:hover {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.212), 0 6px 6px rgba(0, 0, 0, 0.24);
}

.signUp {
  display: flex;
  min-width: 30vw;
  flex-direction: column;

  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.212), 0 6px 6px rgba(0, 0, 0, 0.24);
}
.signUp > * {
  margin: 10px !important;
}
.signUp__container {
  display: flex;
  margin-top: 10%;
  justify-content: center;
}

.notCompletedMilestoneIcon {
  transition: all 0.4s ease-in-out;
  animation-name: grow;
  animation-duration: 1500ms;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: infinite;
  animation-fill-mode: none;
  animation-play-state: running;
}

@keyframes grow {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.3);
  }
  75% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

.MuiTimelineItem-alignAlternate:nth-child(odd) .MuiTimelineItem-content {
  text-align: left;
}

.MuiTimelineItem-alignAlternate:nth-child(even) .milestoneOptionsOpposite {
  flex-direction: row-reverse;
}

a {
  text-decoration: none;
  color: black;
  font-weight: bolder;
}

.goalDetail-header {
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(245, 245, 245, 0.205);
}

